button {
  min-width: 100px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  color: #fff;
  cursor: pointer;
  background: #4676d7;
  border: 0;
  border-radius: 5px;
  margin: 30px auto;
  padding: 8px 16px;
  font-family: inherit;
  display: block;
}

button:hover {
  background: #1d49aa;
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

button[disabled] {
  color: #d2d5db;
  cursor: not-allowed;
  background: #6c7589;
}

